import axios from "axios";

const instance = axios.create({
  baseURL: "https://api.exchangerate.host/",
});

export const convertCurentlyAPI = (
  from: number = 0,
  to: number = 0,
  amount: number = 1
) => {
  return instance
    .get(`convert?from=${from}&to=${to}&amount=${amount}`)
    .then((res: { data: any; }) => res.data);
};

export const latestEndpointAPI = (date: string = "", value: string = "") => {
  return instance
    .get(`${date}?base=${value}`)
    .then((res: { data: any }) => res.data);
};
